@import "../helpers/variables";
@import "../helpers/mixins";

.navbar-container {
  perspective: 600px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 2rem 0;
  z-index: 2;

  .container {
    display: flex;
    justify-content: center;

    .menu-branding--out {
      width: 160px;
      z-index: 3;
      a {
        img {
          width: 100%;
          height: 100%;
        }
      }
    }

    .menu-btn {
      position: absolute;
      z-index: 4;
      right: 35px;
      top: 46px;
      cursor: pointer;
      @include easeout;

      .btn-line {
        display: block;
        width: 36px;
        height: 3px;
        margin: 0 0 6.9px;
        border-radius: 1.5px;
        background-color: #000;
        @include easeout;

        &:nth-of-type(2) {
          width: 28px;
        }
      }

      // rotate into X with menu btn
      &.close {
        transform: rotate(180deg) translateY(5px);

        .btn-line {
          &:nth-child(1) {
            transform: rotate(45deg) translate(4px, 5px);
          }

          &:nth-child(2) {
            width: 0;
          }

          &:nth-child(3) {
            transform: rotate(-45deg) translate(9px, -10px);
          }
        }
      }
    }
  }
  .menu {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    background: $darkColor;
    opacity: 0.96;
    z-index: 3;
    visibility: hidden;
    @include easeout;
    transform: translate3d(100%, 0, 0);

    .menu-nav,
    .nav-item {
      mix-blend-mode: difference;
    }

    &.show {
      @include easeout;
      visibility: visible;
      transform: translate3d(0, 0, 0);
    }
  }
}
